.trust {
	padding-top: 92px;
	padding-bottom: 90px;
	background: url(../img/bg2.jpg) no-repeat center;
	background-size: cover;
	color: #fff;
}

.trust__title {
	margin-bottom: -9px;
}

.trust__img-wrapper {
	margin-bottom: 16px;
	width: 72px;
	display: flex;
	justify-content: center;
	align-items:center;
	flex-wrap: nowrap;
	height: 72px;
	border-radius: 50%;
	background: #fff;
	.trust__img {
		width: 50%;
	}
}

.trust__card {
	margin-top: 69px;
	flex: 0 1 28%;
	min-width: 245px;
	.flex(@flex-direction: column);
	text-align: center;
}

.trust__card-title {
	font-weight: 700;
	margin-bottom: 15px;
}

.trust__card-text {
	font-weight: 300;
}