.callform{
	padding: 90px 0;
	background: url(../img/bg3.jpg) no-repeat center;
	background-size: cover;
	color: #fff;
}

.callform__form {
	margin-top: 60px;
	width: 100%;
	.flex();
}

.callform__btn {
	outline: none;
	border: none;
	color: #fff;
	min-width: 200px;
}

.callform__input {
	padding-bottom: 5px;
	max-width: 271px;
	background: transparent;
	text-align: left;
	border-bottom: 2px solid #d9d9d9;
	&::-webkit-input-placeholder {
		color: #fff;
	}
	&::-moz-placeholder {
		color: #fff;
	}
}

.text-field {
	margin-right: 25px;
}

.callform__input {
	color: #fff;
}