


@media screen and (min-width: 1170px) {
    .exclusive__title {
    	font-size: 44px;
    }

    .exclusive__form-title {
    	font-size: 26px;
    }

    .h2-title {
    	font-size: 36px;
    }
}

@media screen and(max-width: 1184px) {
		body {
			min-height: 430px;
		}

		.header {
			position: relative;
		}
		.header__middle {
			box-shadow: 15px 0px 55px rgba(#ff9c00, 0.6);
			position: relative;
			padding: 30px 20px;
			// display: none;
			position: absolute;
			height: 	100vh;
			min-height: 430px;
			top: 0;
			left: 	-150%;
			background: #ff9c00;
			z-index: 100;
			justify-content: center;
			width: 70%;
			min-width: 320px;
			overflow-y: scroll;
			color: #fff;
		}

		.header__before {
			position: absolute;
			top: 0;
			left: 0;
			width: 100vw;
			height: 100vh;
			z-index: 50;
			background: transparent;
			left: -1000%;
		}

		.header__right-top {
			display: none;
		}

		.header__middle-top {

			flex-direction: column;
			color: #fff;
		}

		.header__menu-list {
			flex-direction: column;
			color: #fff;
		}

		.header__menu-list .header__menu-item {
			margin-right: 	0;
			&:not(:last-child) {
				margin-bottom: 4%;
			}
			

			.header__menu-link {
				color: #fff;
			}
			
		}

		.header__curriculum {
			margin-bottom: 	8%;
			text-align:center;
			color: #fff;
		}

		.header .header__tel_first  {
			margin-bottom: 	6%;
			color: #fff;
		}

		.header .header__tel_second  {
			// margin-bottom: 	6%;
			color: #fff;
		}

		.header__middle-top {
			margin-bottom: 	9%;
			color: #fff;
			margin-top: 8%;
		}

		.header .burger-menu__wrap {
			display: block;	
			margin-left: 25px;
			}

		.burger__close-wrap {
			top: 25px;
			position: absolute;
			margin-left: auto;
			width: 	35px;
			height: 35px;
			// position: relative;
			right: 	25px;
			.burger__close {
				width: 	100%;
				height: 100%;
				// .flex();
				display: flex;	
				justify-content: center;
				align-items: center;
				

			}

			.burger__close-line {
				position: absolute;
				// top: 50%;
				// left: 	50%;
				transform: translate(-50%,-50%);
				width: 25px;
				height: 2px;
				background: #fff;
				&:first-child{
					transform: rotate(135deg);
				}

				&:last-child{
					transform: rotate(45deg);
				}
			}
		}

		.header__middle .header__curriculum, .header__middle .header__phone, .header__middle .header__menu-item {
			opacity: 0;
			position: relative;
			top: -90px;
			left: -140px;
			transform: scale(0);
			transition: opacity 0.25s linear , top 0.25s ease , left 0.25s ease-in , transform 0.25s linear;
			&:first-child {
				transition-delay: 0.15s;
			}

			&:nth-child(2) {
				transition-delay: 0.2s;
			}

			&:nth-child(3) {
				transition-delay: 0.25s;
			}

			&:nth-child(4) {
				transition-delay: 0.3s;
			}

			&:nth-child(5) {
				transition-delay: 0.35s;
			}

			&:last-child {
				transition-delay: 0.40s;
			}
		}

		.header__middle .header__middle-top .header__phone {
			transition-delay: 0.1s;
		}

		.header__middle .header__middle-top .header__curriculum {
			transition-delay: 0.05s;

		}

		.header__middle.active .header__curriculum, .header__middle.active .header__phone, .header__middle.active .header__menu-item {
			opacity: 1;
			left: 0;
			transform: scale(1);
			transition: opacity 0.35s linear , top 0.35s ease , left 0.25s ease-in , transform 0.25s linear;
			position: relative;
			top: 0px;
			&:first-child {
				transition-delay: 0.9s;
			}

			&:nth-child(2) {
				transition-delay: 0.8s;
			}

			&:nth-child(3) {
				transition-delay: 0.7s;
			}

			&:nth-child(4) {	
				transition-delay: 0.6s;
			}

			&:nth-child(5) {	
				transition-delay: 0.5s;
			}

			&:last-child {
				transition-delay: 0.4s;
			}

		}
		.header__menu-item .mobile__btn {
			color: #fff;
			display: block;
		}
		.header__middle.active .header__middle-top .header__phone {
			transition-delay: 0.9s;
		}

		.header__middle.active .header__middle-top .header__curriculum {
			transition-delay: 1s;

		}
}


@media screen and (max-width: 980px) {
	.exclusive__form-title {
		font-size: 22px;
	}

	.calculator__right {
		margin-right: auto;
		margin-left: 	auto;
	}

	.calculator-popup__left {
		margin-bottom: 	65px;
		max-width: 475px;
		margin-right: auto;
		margin-left: 	auto;
	}

	.calculator-popup__right {
		margin-right: auto;
		margin-left: 	auto;
	}

	.calculator__title, .calculator__popup .calculator-popup__title {
		margin-bottom: 90px;
	}

	.exclusive {
		padding-bottom: 60px;
	}

	.catalog {
		margin-top: 60px;
		margin-bottom: 60px;
	}

	.trust, .feedback, .information, .callform, .map, .footer {
		padding-bottom: 60px;
		padding-top: 60px;
	}

	.footer {
		padding-top: 15px;
	}

	.footer__middle, .footer__right, .footer__left {
		margin-top: 45px;
	}

	.calculator {
		padding-bottom: 60px;
		padding-top: 60px;
	}

	.calculator, .calculator__popup {
		.f-container {
			flex-direction: column;
		}
	}

	.calculator__right, .calculator__left-bottom, .calculator__left-top,
	.calculator-popup__right, .calculator-popup__left-bottom, .calculator-popup__left-top
	 {
		width: 100%;
		max-width: 475px;
		// margin: 0 auto;
	}

	.calculator__left-bottom{
		margin: 0 auto;
	}

	.calculator__left-top {
		margin-left: auto;
		margin-right: auto;
	}

	.calculator__left {
		width: 100%;
		margin-bottom: 65px;
	}

	.calculator__text-field:not(:last-child), .calculator__list-handler:not(:last-child) {
		margin-right: 30px;
	}

	// .calculator__range-wrap {
	// 	justify-content: center;
	// }


	.exclusive__form-wrap {
		padding: 35px 30px;
	}
}

