.feedback {
	background: #f9f8f8;
	padding-top: 91px;
	padding-bottom:	91px;
}

.feedback__title {
	margin-bottom: 	50px;
}

.feedback__slider {
	margin-bottom: 	35px;
	max-width: 100%;
	.slick-list{
		margin: 0 -15px;
	}
	.slick-track {
		padding-bottom: 25px;
	}
}

.feedback__slide {
	max-width: 100%;
	background: #fff;
	margin-right: (30px/2);
	margin-left: (30px/2);
	box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);

}


.feedback__slide-contentWrap {
	position: relative;	
	box-shadow: 0px -3px 10px -8px rgba(0,0,0,.45);
	// height: 83%;
	font-size: 14px;
	line-height: 1.58;
	padding-top: 72px;
	padding-right: 20px;
	padding-left: 20px;
	padding-bottom: 38px;
	text-align: center;
}

