.feedback__slide-name {
	text-align: center;
	font-weight: 500;
}

.feedback__slide-imgWrap {
	min-height: 77px;
    position: relative;
	background: #f9f8f8;
	position: relative;
	// box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);

}

.feedback__slide-imgWrap:before {
			position: absolute;	
	content: "";
	width: 	102%;
	height: 100%;
	bottom: 0;
	left: 	-1%;
	background:	rgb(249, 248, 248);
}

.feedback__slide-img {
	width: 100px;
    height: 100px;
    z-index: 10;
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
}
.feedback__slider-stars {
	display: flex;
	margin: 0 auto;
	justify-content: center;
	transform: rotate(180deg);
}

.feedback__slider-star {
	transform: rotate(180deg);
	width: 13px;
	height: 13px;
	background: url(../img/star.svg) no-repeat center;
	background-size: cover;
	filter: grayscale(1);
	transition: all 0.2s linear;
	&:hover, &:hover ~ & {
		filter: grayscale(0);
	}
}

.no-filter {
filter: grayscale(0);
}

.feedback__slide-text {
	margin-bottom: 17px;
	font-size: 14px;
	letter-spacing: 0.25px;
	color: #737373;
	line-height: 1.95;
	// line-height: 27px;
}

.feedback__slider-stars {
	margin-bottom: 21px;
}

.feedback__arrows {
	position: relative;	
	.flex();
	max-width: 84px;
	margin: 	0 auto;
	// height: 1px;
	// &:before {
	// 	display: block;	
	// 	padding-top: 34px;
	// 	content:"";
	// }
}

.next-arrow, .prev-arrow {
	position: absolute;
	width: 	34px;
	height: 23px;
	svg	{
		&:hover {
			path{
				fill: #ff9c01!important;
			}
			
		}
		cursor: pointer;
		path{
		fill: #d9d9d9!important;

		transition: all 0.3s linear;
		
	}
	}
	
}

.next-arrow {
	left: 	0;
	top: 	0;
	transform: rotate(180deg) translateY(-50%);
}

.prev-arrow {

	right: 	0;
	top:	0;
}
