@import url('app/less/_fonts.less');


html, body {
	overflow-x: hidden; 

}
body 
{
	margin: 0;
	padding: 0;	
	font-family: 'Roboto', sans-serif;
	font-size: 16px;
	font-weight: 400;
	letter-spacing: 1.15px;



}

div, a {
	outline: none;
}



*, *:before, *:after {
	box-sizing: border-box;
}

img
{
	max-width: 100%;
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
	padding: 0;
	margin: 0;
}

h2 {

	overflow-wrap: break-word;
}


ul , li {
	list-style: none;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
	padding: 0;
	margin: 0;
	display: block;
	color: #fff;
	cursor: pointer;

}


input {
	width: 100%;
	outline: none;
	border: none;
	text-align: center;
}


main {
	color:  #242424;
}

.center {
	max-width: 1220px;
	width: 100%;
	margin: 0 auto;
	padding: 0 25px;

}



.f-container {

	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	flex-direction: row;
}


.flex(@justify-content: space-between, @align-items: center, @flex-direction: row, @flex-wrap: nowrap){
	display: flex;
	justify-content: @justify-content;
	align-items:  @align-items;
	flex-direction:  @flex-direction;
	flex-wrap:  @flex-wrap;
}




.header {
	position: relative;
	letter-spacing: 0.15px;
	padding-top: 33px;
	padding-bottom: 18px;
	.center {
		color: #242424;
	}

	a {
		color: #242424;
	}

	.f-container {
		align-items: flex-start;
	}
	
}

.header__top, .header__bottom {
	.flex();
	width: 100%;
	margin-bottom: 29px;
}

.header__menu, .header__menu-list {
	width: 100%;
	max-width: 630px;
	margin: 0 auto;
}

.header__menu-list {
	.flex();
}

.header__menu-item {
	cursor: pointer;
	&:not(:last-child) {
		margin-right: 15px;
	}
}

.header__left, .header__middle {
	margin-right: 25px;
}

.header__btn {
	margin-left: auto;
	border: 1px solid #9e8b48;
	max-width: 138px;
	border-radius: 25px;
	padding: 15px;
	padding-top: 13px;
	padding-bottom: 13px;
	font-size: 13px;
	transition: all 0.25s linear;
	&:hover {
		background: #9e8b48;
		color: #fff;
	};
}

.header__menu-link {
	transition: all 0.25s linear;
	text-transform: uppercase;
	font-size: 13px;
	&:hover {
		color: #9e8b48;
	}
}

.header__search {
	margin-top: 18px;
	width: 100%;
	max-width: 200px;
	border-bottom: 1px solid #242424;
	position: relative;
	.header__search-before {
		position: absolute;
		cursor: pointer;
		// content:"";
		width: 16px;
		height: 16px;
		background: url(../img/magnifying-glass.svg) no-repeat center;
		background-size: contain;
		right: 0;
		top: -1px;
	}
}

.header__search-input {
	text-align: left;
	
	max-width: 88%;
}


.header__tel_second, .header__tel_first {
	font-size: 14px;
}

.header__curriculum {
	font-size: 14px;
}

.header__phone {
	position: relative;
	&:before {
		position: absolute;
		top: 0;
		left: -23px;
		content: "";
		width: 14px;
		height: 14px;
		background: url(../img/call-answer.svg) no-repeat center;
		background-size: cover;
	}
}

.header__middle {
	.flex(@flex-direction:column);
	flex: 0 1 738px;
	padding-top: 16px;
}

.header__middle-top {
	margin-bottom: 38px;
	.flex();
	width: 100%;
}

//|||||||||||||||||||COMMON ELEMENTS|||||||||||||||||||//
.main-btn {
	background: #ff9c00;
	font-size: 15px;
	border-radius: 50px;
	width: 	100%;
	max-width: 280px;
	text-align: center;
	padding-top: 21px;
	padding-bottom: 21px;
	transition: all 0.35s ease;
	box-shadow:5px 2px 20px rgba(0,0,0,.3);
	&:hover {
		box-shadow: none;
		background: #ff9c00;
	}
}

.h2-title {
	font-size: (36vw/1170*100);
	font-weight: 500;
	text-align: center;
}

.burger-menu__wrap {
	width: 35px;
	cursor: pointer;
	height: 35px;
	.burger-menu {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content:center;
		align-items:center;
	}
}
.burger__menu-line {
	display: block;
	width: 25px;
	height: 2px;
	background: #242424;
	margin-left: auto;
	border-radius: 15px;
	&:not(:last-child) {
		margin-bottom: 5px;
		width: 	15px;

	}

	&:first-child {
		width:  23px;
	}

}



.header .burger-menu__wrap {
	display: none;	
}
.header {
	.burger-menu__wrap.visible {
	display: none;
}
}

.header__menu-item .mobile__btn {
			color: #fff;
			display: none;
		}

//|||||||||||||||||||END COMMON ELEMENTS|||||||||||||||||||//








