.information{
	padding: 90px 0;
	.f-container {
		align-items: stretch;
		flex-wrap: nowrap;
	}
}
.information__btn {
	margin: 0 auto;
	margin-top: 61px;
	
}

.information__card {
	margin: 0 auto;
	&:nth-child(2n) {
		margin-right: 20px;
		margin-left: 	20px;
	}
	margin-top: 51px;
	width: 100%;
	max-width: 374px;
	.flex(@flex-direction:column, @align-items:flex-start);
}

.information__card-more {
	font-weight: 500;
	letter-spacing: 0.15px;
	font-weight: 500;
	font-size: 14px;
	color: #242424;
	transition: all 0.25s ease-out;
	&:hover {
		color: #9e8b48;
	}
}

.information__card-img-wrap, .information__card-date {
	margin-bottom: 	21px;
}

.information__title {
	
}

.information__card-date {
	letter-spacing: 0.15px;
}

.information__card-text {
	margin-bottom: 	17px;
	letter-spacing: 0.25px;
	font-size: 14px;
	line-height: 1.45;
}

.information__card-title {
	margin-bottom: 	14px;
	font-size: 20px;
	font-weight: 500;
}