.footer {
	padding: 90px 0;
	background: #2c2a2f;
	color: #fff;
	font-weight: 14px;
	letter-spacing: 0.25px;
	.f-container {
		align-items:flex-start;
	}
}

.footer-h3 {
	text-transform: uppercase;
	font-size: 14px;
	font-weight: 500;
	margin-bottom: 27px;
	letter-spacing: 0.15px;
}

.footer__up-btn {
	color: #9e8b48;
	text-transform: uppercase;
	font-size: 14px;
	font-weight: 500;
}

.footer__social-list-item {
	&:nth-child(2) {
		svg{
			width: 20px;
			height: 20px;
		}
	}
}

.footer__social-link {
	width: 38px;
	height: 38px;
	border-radius: 50%;
	background: #fff;
	transition: all 0.25s linear;
	.flex(@justify-content:center);
	svg{
		width: 15px;
		height: 15px;
		fill: #9e8b48;
		transition: all 0.25s linear;
	}

	&:hover {
		background: #ff9c00;
		svg {
			fill: #fff;
		}
	}
}

.footer__social-list {
	.flex();
	width: 147px;
	margin-bottom: 44px;
}

.footer__nav-item {
	
	&:not(:last-child){
		margin-bottom: 11px;
	}
	
	

}

.footer__nav-link {
	transition:all 0.25s linear;
	&:hover {
		color: #9e8b48;
	}
}

.footer__left {
	max-width: 280px;
	margin-right: 25px;
}

.footer__middle {
	margin-right: 25px;
}

.footer__contacts-place {
	line-height: 1.45;
	margin-bottom: 20px;
}

.footer__tel {
	line-height: 1.55;
}